import React, { memo } from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";

const BlogCard = ({ slug, name, shortDesc, className, thumbnail }) => {
  return (
    <Link to={`/blog/${slug}`} className={className}>
      <div className="blog__card">
        <div className="blog__card__img">
          <img src={thumbnail} alt={name} />
        </div>
        <div className="blog__card__body">
          <div className="blog__card__body__title">
            <p className="fs__title">{name}</p>
          </div>
          <div className="blog__card__body__content">
            <p className="fs__text">
              Lorem ipsum dolor sit amet consectetur adipisicing elit. Delectus minus debitis
              consequuntur nemo optio rem.
            </p>
          </div>
        </div>
      </div>
    </Link>
  );
};

export default memo(styled(BlogCard)`
  .blog__card {
    /* $radius: 12px; */
    background-color: ${(props) => props.theme.colorCard};
    border-radius: 12px;
    height: 100%;
    &__img {
      > img {
        border-top-left-radius: 12px;
        border-top-right-radius: 12px;
        width: 100%;
        height: 200px;
        object-fit: cover;
      }
    }
    &__body {
      padding: 20px;
      &__title {
        line-height: 30px;
        font-weight: bolder;
        height: 100px;
        p {
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 3;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
      &__content {
        p {
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 3;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }
  }
`);
