import React from "react";
import styled from "styled-components";
import BlogCard from "../../common/components/BlogCard";
import { useBlogs } from "../../common/hooks/useBlogs";
import { chunkMap } from "../../helper/chunkMap";

const Blogs = ({ className }) => {
  const { data: blogs } = useBlogs(6);
  return (
    <div className={className}>
      <header className="container">
        <p className="fs__title--main text-center fw-bolder mt-5">Blog</p>
        <p className="fs__text text-center w-lg-50 mx-auto">
          Lorem ipsum, dolor sit amet consectetur adipisicing elit. Delectus veniam, eum quo sed
          voluptatum neque aliquid dolorem, error quidem officia temporibus voluptatibus minus
          eligendi architecto.
        </p>
      </header>

      <main className="container">
        {blogs &&
          chunkMap(blogs.blogs, 3, (chunk, index) => {
            return (
              <div key={index} className="row mt-lg-5">
                {chunk.map((blog) => (
                  <div className="col-12 col-lg-4 mb-4 mb-lg-0" key={blog.id_blog}>
                    <BlogCard name={blog.name_b} slug={blog.slug} thumbnail={blog.thumbnail} />
                  </div>
                ))}
              </div>
            );
          })}
      </main>
    </div>
  );
};

export default styled(Blogs)`
  flex: 1;
`;
